/* eslint-disable */

import React, { useState } from "react";
import { useParams, useHistory, Link, Route } from "react-router-dom";
import Questions from "../questions";
import { useSelector, useDispatch } from "react-redux";

function GetType() {
  let { id } = useParams();
  let found = Questions.find((x) => x.id == id);
  let history = useHistory();
  let dispatch = useDispatch();
  let [answer, setAnswer] = useState(0);

  return (
    <>
      <div className="content">
        <h3>{found.question}</h3>
        {found.answer.map((a, i) => {
          return (
            <div key={i}>
              <button
                onClick={() => {
                  setAnswer(i);
                }}
              >
                {a}
              </button>
              <br></br>
            </div>
          );
        })}
      </div>
      {id != 0 ? (
        <button
          onClick={() => {
            history.goBack();
          }}
        >
          
        </button>
      ) : null}

      {id != 12 ? (
        <button
          onClick={() => {
            //   store 
            dispatch({ type: "ADD_ANSWER", payload: found.result[answer] }); // found      r 
          }}
        >
          <Link to={`/test/${parseInt(id) + 1}`}> </Link>
        </button>
      ) : null}
      {id == 12 ? (
        <button
          onClick={() => {
            // 12        
            dispatch({ type: "ADD_ANSWER", payload: found.result[answer] });
            dispatch({ type: "POST_ANSWER" });
          }}
        >
          
        </button>
      ) : null}
    </>
  );
}
export default GetType;
